<template>
  <div>
    <el-container
      ><el-header>{{ this.$route.meta.title }}</el-header></el-container
    >

    <el-menu
      :default-active="this.$route.path"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      router
      background-color="#fff"
      text-color="#409EFF"
      active-text-color="#0084ff"
      width="100%"
    >
      <el-menu-item v-for="(item, i) in navList" :key="i" :index="item.name">
        <template slot="title">
          <span> {{ item.navItem }}</span>
        </template>
      </el-menu-item>
    </el-menu>
    <el-main class="detailed-content">
      <el-header
        ><el-input placeholder="请输入内容" v-model="cname"></el-input
        ><el-button type="primary" icon="el-icon-search" @click="serach"
          >搜索</el-button
        >
      </el-header>
      <router-view :blurList="alist" />
    </el-main>
  </div>
</template>
<script>
import Customer from "../quasiCustomer/index.vue";
export default {
  components: {
    Customer
  },
  data() {
    return {
      cname: "",
      alist: [],
      navList: [
        { name: "/localUser", navItem: "本地客户" },
        { name: "/quasiCustomer", navItem: "准客户" },
        { name: "/customer", navItem: "客户" }
      ]
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      //   console.log(key, keyPath);
    },
    serach() {
      if (this.$route.path == "/localUser") {
        this.axios
          .get(`${this.baseUrl}client/queryNoId`, {
            params: { cname: this.cname }
          })
          .then(res => {
            this.alist = res.data;
          })
          .catch(err => {});
      } else if (this.$route.path == "/quasiCustomer") {
        this.axios
          .get(`${this.baseUrl}client/queryLocal`, {
            params: { cname: this.cname }
          })
          .then(res => {
            console.log(res);
            this.alist = res.data;
          })
          .catch(err => {});
      } else {
        this.axios
          .get(`${this.baseUrl}client/queryAllClients`, {
            params: { cname: this.cname }
          })
          .then(res => {
            console.log(res);
            this.alist = res.data;
          })
          .catch(err => {});
      }
    }
  }
};
</script>
<style scoped>
.el-menu-item {
  width: 33%;
  text-align: center;
}
.el-input {
  width: 72%;
}
.el-header,
.el-footer {
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  color: #333;
  text-align: center;
  line-height: 200px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>



