import Vue from "vue";
import VueRouter from "vue-router";
import report from "@/view/nav/report";

Vue.use(VueRouter);
export default new VueRouter({
  //   mode: "history",
  routes: [
    {
      name: "report",
      path: "/",
      component: report,
      redirect: "/localUser",
      children: [
        {
          name: "localUser",
          path: "/localUser",
          component: () => import("@/view/local/index"),
          meta: { title: "本地客户列表" }
        },
        {
          name: "quasiCustomer",
          path: "/quasiCustomer",
          component: () => import("@/view/quasiCustomer/index"),
          meta: { title: "准客户列表" }
        },
        {
          name: "customer",
          path: "/customer",
          component: () => import("@/view/customer/index"),
          meta: { title: "客户列表" }
        }
      ]
    },
    {
      path: "/localDetails",
      name: "localDetails",
      component: () => import("@/view/local/details"),
      meta: { title: "本地客户详情" }
    },
    {
      path: "/localEdit",
      name: "localEdit",
      component: () => import("@/view/local/edit"),
      meta: { title: "编辑本地客户信息" }
    },
    {
      path: "/addUser",
      name: "addUser",
      component: () => import("@/view/newUser/index"),
      meta: { title: "新增客户" }
    },
    {
      path: "/confirm",
      name: "confirm",
      component: () => import("@/view/newUser/confirm"),
      meta: { title: "新增确认" }
    },
    {
      path: "/customerDetails",
      name: "customerDetails",
      component: () => import("@/view/quasiCustomer/details"),
      meta: { title: "准客户详情" }
    },
    {
      path: "/customerEdit",
      name: "customerEdit",
      component: () => import("@/view/quasiCustomer/edit"),
      meta: { title: "编辑准客户信息" }
    }
  ]
});



